.footer-wrapper {
  width: 100%;
  background-color: #1d1d1d;
  color: #fff;
  padding: 32px 0 32px;
  font-family: "Encode Sans Expanded";
  font-weight: 300;
  box-sizing: border-box;
  overflow: hidden;
}

.footer-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0;
}
.footer-logo {
  max-width: 150px;
  height: auto;
  margin-bottom: 0px;
  object-fit: contain;
}

/* ---- Col hizalama ---- */
.footer-col {
  display: flex;
  align-items: flex-start;
}

.footer-col--center {
  justify-content: center;
}

/* İç bloklar */
.footer-link-block,
.footer-brand-block {
  width: 100%;
  max-width: 346px;
  display: flex;
  flex-direction: column;
}

/* Logo */
.footer-logo-placeholder {
  width: 100%;
  max-width: 210px;
  aspect-ratio: 3 / 1;
  background-color: #ccc;
  margin-bottom: 29px;
}

/* Açıklama */
.footer-desc {
  font-weight: 300;
  font-size: clamp(12px, 1.2vw, 16px);
  line-height: 1.4;
  text-transform: uppercase;
  margin-bottom: 16px;
  margin-left: 12px;
}

/* Çizgiler */
.footer-line,
.footer-line2 {
  width: 100%;
  max-width: 346px;
  height: 1px;
  background-color: #fff;
  margin-bottom: 0px;
  margin-left: 12px;
}

.footer-line2 {
  max-width: 105px;
  margin: 8px 0;
}

/* Başlık */
.footer-section-title {
  font-weight: 300;
  font-size: clamp(14px, 1.3vw, 16px);
  text-transform: uppercase;
  margin-bottom: 16px;
  margin-left: 12px;
  margin-top: 32px;
}
.footer-contact-icon {
  width: 20px;
  height: 20px;
  object-fit: contain;
  flex-shrink: 0;
}

/* Linkler */
.footer-link-block p:not(.footer-section-title):not(:first-of-type) {
  margin-top: 14px;
}

.footer-link-block p {
  font-size: clamp(12px, 1.1vw, 14px);
  font-weight: 300;
  cursor: pointer;
  margin: 0;
}

.footer-link-block p:hover {
  color: #aaa;
}

/* İletişim */
.footer-contact-list {
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-bottom: 0px;
  margin-left: 12px;
}

.footer-contact-item {
  display: flex;
  align-items: center;
  gap: 13px;
  font-size: clamp(12px, 1.1vw, 14px);
}

/* İkonlar (geçici) */
.icon.phone::before {
  content: "📞";
}

.icon.mail::before {
  content: "✉️";
}

.icon.location::before {
  content: "📍";
}

/* Sosyal Medya */
.footer-social-block {
  margin-top: 0px;
}

.footer-divider {
  width: 346px;
  height: 1px;
  background: #fff;
  margin: 8px 0 16px 12px;
  /* üstüne dayalı ve sola yaslı */
}

.footer-social {
  display: flex;
  gap: 16px;
  margin-left: 12px;
}
.social-icon {
  width: 30px;
  height: 30px;
  object-fit: contain;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.social-box {
  width: 24px;
  height: 24px;
  background-color: #fff;
}

/* ------- Özel hizalamalar -------- */

/* PROJELERİMİZ: ortala (başlık, çizgi, linkler) */
.footer-links--projects {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  /* alt maddeler ve çizgi sağa yaslanır */
  text-align: right;
  margin-top: 45px;
}

.footer-links--projects .footer-line2 {
  margin-left: auto;
  /* çizgi sağdan başlar */
}

/* HIZLI ERİŞİM: sağa yasla */
.footer-link-block--right {
  margin-left: auto;
  align-items: flex-end;
  text-align: right;
  margin-right: 12px;
  margin-top: 45px;
}
/* Footer linkleri p ile aynı gözüksün */
.footer-link-block a {
  display: block;           /* p gibi alt alta gelsin */
  margin: 0 0 16px 0;        /* eski boşlukları koru */
  font-size: inherit;       /* p ile aynı boyutta */
  font-weight: inherit;     /* kalınlık aynı */
  color: inherit;           /* mavi yerine mevcut yazı rengini kullan */
  text-decoration: none;    /* alt çizgiyi kaldır */
  cursor: pointer;
}

.footer-link-block a:hover {
  text-decoration: underline; /* opsiyonel, hover efekti */
}
.footer-link-block--right .footer-line2 {
  margin-left: auto;
}

/* ------- Bottom ------- */

/* Sadece “uzun divider” (üst çizgi) — altındaki metinleri etkilemez */
.footer-bottom-line {
  height: 1px;
  background: #fff;
  opacity: 0.5;
  /* Sağ/sol boşluğu burada rahatça ayarlayın */
  margin: 40px 200px 0 200px;
  /* ör: 200px */
}

/* Alt metin satırı */
.footer-bottom {
  padding-top: 20px;
  font-size: 14px;
  font-weight: 400;
  line-height: 100%;
}

.footer-bottom-inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer-bottom-left {
  text-align: left;
  margin-left: 12px;
}

.footer-bottom-right {
  display: flex;
  gap: 35px;
  margin-right: 12px;
  /* aralarındaki boşluk 35px */
}

.footer-bottom-right a {
  color: #fff;
  text-decoration: none;
  font-size: 14px;
  font-weight: 400;
}

.footer-bottom-right a:hover {
  color: #aaa;
}
/* Responsive */
@media (max-width: 768px) {
  .footer-wrapper {
    padding-left: 16px;   /* ✅ tüm içerikler için tek hiza */
    padding-right: 16px;  /* sağdan da eşitle */
    font-family: 'Encode Sans Expanded'; /* ✅ sadece Encode Sans Expanded */
  }

  .footer-col {
    justify-content: flex-start; /* ✅ sola yasla */
    align-items: flex-start;
  }

  .footer-brand-block {
    align-items: flex-start;
    text-align: left;   
    margin-left: 0px;      
    margin-right: 0;
    font-family: 'Encode Sans Expanded';
  }

  .footer-link-block,
  .footer-contact-list,
  .footer-social,
  .footer-bottom-left,
  .footer-bottom-right {
    align-items: flex-start;
    text-align: left;   
    margin-left: 12px;
    margin-right: 0;
    font-family: 'Encode Sans Expanded';
  }

  /* Divider mobilde sola hizalı */
  .footer-line,
  .footer-line2,
  .footer-divider,
  .footer-bottom-line {
    margin-left: 12px; 
    margin-right: 0;
  }

  /* === PROJELERİMİZ & HIZLI ERİŞİM BLOKLARI === */
  .footer-links-wrapper {
    display: flex;
    flex-direction: column; /* ✅ alt alta */
    gap: 32px;              
    margin-top: 24px;
    font-family: 'Encode Sans Expanded';
  }

  .footer-link-block {
    align-items: flex-start;
    text-align: left;
    margin: 0;
    margin-left: 12px;
  }

  .footer-link-block .footer-section-title {
    margin-bottom: 8px;
    font-family: 'Encode Sans Expanded';
  }

  /* Divider başlığın hemen altında */
  .footer-link-block .footer-line2 {
    width: 100px; 
    margin: 8px 0 16px 0;
  }

  /* === BOTTOM === */
  .footer-bottom-inner {
    flex-direction: column;
    align-items: flex-start; 
    gap: 12px;
    font-family: 'Encode Sans Expanded';
  }

  .footer-bottom-right {
    gap: 20px;
    flex-wrap: wrap;
  }
}
